import cmdIcon from '../cmd-icon/cmd-icon.vue'

export default {
  name: 'cmd-avatar',

  components: {
    cmdIcon
  },

  props: {
    /**
     * 大小 sm md lg Number
     */
    size: {
      validator: val => {
        return typeof val === 'number' || ['sm', 'lg', 'md'].includes(val);
      },
      default: 'md'
    },
    /**
     * 形状 circle圆形 square矩形
     */
    shape: {
      validator: val => ['circle', 'square'].includes(val),
      default: 'circle'
    },
    /**
     * 样式属性style
     */
    make: {
      type: Object,
      default: () => {
        return {
          'color': ''
        };
      }
    },
    /**
     * 图片网络地址
     */
    src: {
      type: String,
      default: ''
    },
    /**
     * 图标type名称
     */
    icon: {
      type: String,
      default: ''
    },
    /**
     * 文本名字建议取首位text[0],长度小于3位
     */
    text: {
      type: [String,Number],
      default: ''
    }
  },

  data() {
    const elId = `Cmd_${Math.ceil(Math.random() * 10e5).toString(36)}`
    return {
      elId: elId,
      iconSize: 40
    }
  },

  computed: {
    // 形状大小
    setShapeSizeClass() {
      let classList = []
      if (['circle', 'square'].includes(this.shape)) {
        classList.push(`cmd-avatar--${this.shape}`)
      }
      if (['sm', 'lg', 'md'].includes(this.size)) {
        classList.push(`cmd-avatar--${this.size}`)
      }
      return classList
    },
    // 自定义大小
    setNumSizeStyle() {
      return typeof this.size === 'number' ?
        `width:${this.size}px;` +
        `height:${this.size}px;` +
        `font-size:${(this.size / 2)}px;` +
        `line-height:${this.size}px;` :
        '';
    },
    // 图标文本样式
    setIconTextStyle() {
      let styleString = '';
      for (let it in this.make) {
        styleString += `${it}:${this.make[it]};`;
      }
      return styleString;
    }
  },













  onReady() {
    // 当前对象el
    let view = uni.createSelectorQuery().select(`#${this.elId}`);
    view.fields({
      size: true
    }, data => {
      this.iconSize = data.width / 2;
    }).exec();
  },


  methods: {
    // 当图片载入完毕时
    $_imageLoad(e) {
      this.$emit('load', e);
    },
    // 当错误发生时
    $_imageError(e) {
      this.$emit('error', e);
    },
    // 点击事件
    $_click(e) {
      this.$emit('click',e)
    }
  }
}
